  }
} ;

.main-footer {
  display: flex;
  position: fixed;
  bottom: 0px;
  left: 0px;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 50px;
  background: linear-gradient(
      180deg,
      var(--ft-gradient-a),
      var(--ft-gradient-b)
    ),
    var(--footer-color-dark);
}
#copyright {
  position: fixed;
  bottom: 50px;
  padding: 0;
  right: 0;
  margin: 0;
  font-size: 10px;
  padding-right: 5px;
  text-align: end;
  font-weight: var(--text-copyRight-dark);
}
.sm {
  position: fixed;
  bottom: 60px;
  left: 10px;
  display: flex;
  flex-direction: column;
}
.smIcon {
  -webkit-filter: var(--shadow-sm-dark);
          filter: var(--shadow-sm-dark);
  height: 30pt;
  width: 30pt;
  padding: 5px;
  display: block;
}
.footer {
  display: flex;
  list-style: none;
  flex-direction: row;
  align-items: center;
  margin: 0;
  padding: 0;
}
.footer .flink {
  color: var(--ft-color-dark);
  -webkit-filter: var(--shadow-ft-dark);
          filter: var(--shadow-ft-dark);
  padding-top: 4px;
  padding-bottom: 4px;
  padding-left: 10px;
  padding-right: 10px;
}
.flags {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.flags img {
  height: 20px;
  width: 30px;
  -webkit-filter: var(--shadow-ft-dark);
          filter: var(--shadow-ft-dark);
}
.toggle {
  height: 50px;
  width: 50px;
}
#toggle {
  -webkit-filter: var(--shadow-ft-dark);
          filter: var(--shadow-ft-dark);
  height: 50px;
  width: 50px;
}
#toggle:hover {
  transform: rotateY(180deg);
}
.flink:hover {
  background-color: var(--ft-dark-hover-color);
  border-radius: 10px;
}
#fre:hover,
#esp:hover,
#eng:hover {
  transform: scale(2);
  transition: transform 1s;
}
